import React, { useState } from 'react';

const categorias = [
  'Doctrina católica',
  'Citas bíblicas',
  'Ejemplos prácticos',
  'Testimonios',
  'Datos científicos',
  'Videos de Youtube'
];

export default function PanelCategoriasGemini({ resultados }) {
  const [openCat, setOpenCat] = useState({});
  return (
    <div style={{padding:'24px',background:'rgba(255,255,255,0.9)',borderRadius:'15px',boxShadow:'0 8px 32px rgba(0,0,0,0.1)',backdropFilter:'blur(10px)',height:'100%',overflow:'auto'}}>
      <strong style={{fontSize:'1.1rem',marginBottom:8,display:'block'}}>Categorías encontradas:</strong>
      {categorias.map(cat => {
        const items = resultados[cat] || [];
        return (
          <div key={cat} style={{marginBottom:'12px'}}>
            <div
              style={{color:'#4285F4',cursor:'pointer',display:'flex',alignItems:'center',gap:'8px',fontWeight:600,fontSize:'1.05rem'}}
              onClick={() => setOpenCat(prev => ({...prev, [cat]: !prev[cat]}))}
            >
              {cat} <span style={{fontSize:'1em',color:'#666',fontWeight:'400'}}>({items.length})</span> {openCat[cat] ? '▼' : '▶'}
            </div>
            {openCat[cat] && (
              <ul style={{paddingLeft:'18px',marginTop:'8px'}}>
                {items.length === 0 ? (
                  <li style={{color:'#aaa',fontStyle:'italic'}}>Sin resultados</li>
                ) : items.map((linea, idx) => (
                  <CategoriaResultado key={idx} texto={typeof linea === 'string' ? linea : linea.texto} fuente={linea.fuente} />
                ))}
              </ul>
            )}
          </div>
        );
      })}
    </div>
  );
}

function CategoriaResultado({ texto, fuente }) {
  const [expandido, setExpandido] = useState(false);
  // Mostrar solo dos líneas (aprox 180 caracteres) y botón Leer más
  const resumen = texto.length > 180 ? texto.slice(0,180).replace(/\n/g,' ') : texto;
  return (
    <li style={{marginBottom:'6px'}}>
      <div style={{marginBottom:4, overflow:'hidden', display:'-webkit-box', WebkitLineClamp: expandido ? 'none' : 2, WebkitBoxOrient:'vertical', whiteSpace:'pre-line'}}>
        {!expandido ? (
          <>
            {resumen}{texto.length > 180 && <>... <button style={{marginLeft:8}} onClick={()=>setExpandido(true)}>Leer más</button></>}
          </>
        ) : (
          <>
            {texto} <button style={{marginLeft:8}} onClick={()=>setExpandido(false)}>Mostrar menos</button>
          </>
        )}
      </div>
      <div style={{display:'flex',gap:12}}>
        {fuente && <a href={fuente} target="_blank" rel="noopener noreferrer">Ver fuente</a>}
      </div>
    </li>
  );
}
