
import React from 'react';


import PanelResizer from './components/PanelResizer';
import PanelCategoriasGemini from './components/PanelCategoriasGemini';
import PanelGeminiEditor from './components/PanelGeminiEditor';

function App() {
  const [resultadosGemini, setResultadosGemini] = React.useState({});
  return (
    <div className="App" style={{height:'100vh',width:'100vw',overflow:'hidden',display:'flex',flexDirection:'column'}}>
      {/* Header / Menú Superior */}
      <header style={{
        background: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)',
        color: 'white',
        padding: '1rem',
        marginBottom: '0',
        flexShrink: 0
      }}>
        <nav style={{
          display: 'flex',
          justifyContent: 'space-between',
          alignItems: 'center',
          maxWidth: '1200px',
          margin: '0 auto'
        }}>
          <h1 style={{ 
            margin: 0,
            fontSize: '1.8rem',
            fontWeight: 'bold'
          }}>
            El Predicador Católico
          </h1>
          <div style={{ display: 'flex', gap: '1rem' }}>
            <button style={{
              background: 'rgba(255,255,255,0.2)',
              color: 'white',
              border: '2px solid rgba(255,255,255,0.3)',
              padding: '8px 16px',
              borderRadius: '6px',
              cursor: 'pointer',
              fontSize: '0.9rem'
            }}>
              Preparar sermón
            </button>
            <button style={{
              background: 'rgba(255,255,255,0.2)',
              color: 'white',
              border: '2px solid rgba(255,255,255,0.3)',
              padding: '8px 16px',
              borderRadius: '6px',
              cursor: 'pointer',
              fontSize: '0.9rem'
            }}>
              Sermones preparados
            </button>
            <button style={{
              background: 'rgba(255,255,255,0.2)',
              color: 'white',
              border: '2px solid rgba(255,255,255,0.3)',
              padding: '8px 16px',
              borderRadius: '6px',
              cursor: 'pointer',
              fontSize: '0.9rem'
            }}>
              Configuración
            </button>
            <button style={{
              background: 'rgba(255,255,255,0.2)',
              color: 'white',
              border: '2px solid rgba(255,255,255,0.3)',
              padding: '8px 16px',
              borderRadius: '6px',
              cursor: 'pointer',
              fontSize: '0.9rem'
            }}>
              Login
            </button>
          </div>
        </nav>
      </header>
      {/* Layout Principal con Dos Paneles */}
      <PanelResizer
        left={
          <PanelGeminiEditor setResultadosGemini={setResultadosGemini} />
        }
        right={
          <PanelCategoriasGemini resultados={resultadosGemini} />
        }
      />
    </div>
  );
}

export default App;


